import { Application } from "pixi.js";

//  canvas
export const game = new Application({
    width: 800,
    height: 950,
    antialias: true,
    transparent: false,
    resolution: 1,
});


//  canvas  html  body 
document.body.append(game.view);

// 
export function getRootContainer() {
    return game.stage;
}